body {
  margin: 0;
  padding: 0;
  background: #182C61;
  height: 90vh;
  text-align: center;
  margin: 5px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

.status {
  font-size: 20px;
  background:linear-gradient(to right, #005c97, hsl(239, 47%, 40%));
  color: #fff; 
  display: inline-block;
  font-weight: bold;
  word-spacing: 5px;
  margin-bottom: 15px;
  width: 22rem;
  border-radius: 20px;
}


.squareGrid {
  background-color: #3867d6;
  padding: 8px;
  font-size: 3em;
  text-align: center;
  cursor: pointer;
  width: 4rem;
  height: 4rem;
  display: block;
  
}

.main-board {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
}

.circle {
   border-radius: 100%;
   padding-top: 98%;
   animation-iteration-count: 1;
  animation-name: add-coin;
  animation-duration: .3s; 

}

.btn-restart {
  margin-top: 1.7rem;
  border: none;
  cursor: pointer;
  display: inline-block;
  height: 4rem;
  width: 20rem;
  color: #fff;
  font-weight: bold;
  font-size: 1.5rem;
  transition: all ease 0.3s;
  /* color: #fff; */
  outline: none;
  background: #373B44;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #4286f4, #373B44);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #4286f4, #373B44); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
border-radius: 20px;
}
.btn-restart:hover {
  text-decoration: none;
  border: 3px solid #3867d6;
  background: #3867d6;
}  





@media screen and (min-width: 320px) and (max-width: 669px){
  .squareGrid {
    padding: 5px;
    font-size: 2em;
    text-align: center;
    cursor: pointer;
    width: 3rem;
    height: 3rem;
  }
  .btn-restart {
    margin-top: 1.7rem;
    border: none;
    cursor: pointer;
    display: inline-block;
    height: 2rem;
    width: 10rem;
    color: #fff;
    font-weight: bold;
    font-size: 1.5rem;
  
  }

  .btn-restart:hover {
    text-decoration: none;
    border: 3px solid #3867d6;
    background: #3867d6;
  }  
  .status {
    width: 10rem; }

}

@media screen and (min-width: 360px) and (max-width: 640px){

  .squareGrid {
    padding: 4px;
    font-size: 2em;
    text-align: center;
    cursor: pointer;
    width: 2.5rem;
    height: 2.5rem;
  }
}

@media screen and (min-width: 320px) and (max-width: 568px){
  .squareGrid {
    padding: 3px;
    font-size: 2em;
    text-align: center;
    cursor: pointer;
    width: 2rem;
    height: 2rem;   
  }
}